.bx--cc--chart-wrapper {
  font-family: 'IBM Plex Sans Condensed', 'Helvetica Neue', Arial, sans-serif;
}
.bx--cc--chart-wrapper p {
  padding: 0;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  color: #525252;
}
.bx--cc--chart-wrapper text {
  font-size: 12px;
  font-weight: 400;
  fill: #525252;
}
.bx--cc--chart-wrapper g.gauge-numbers text.gauge-value-number {
  font-family: 'IBM Plex Sans', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 300;
}
.bx--cc--chart-wrapper text.meter-title,
.bx--cc--chart-wrapper text.percent-value {
  font-size: 16px;
  font-family: 'IBM Plex Sans', 'Helvetica Neue', Arial, sans-serif;
}
.bx--cc--chart-wrapper text.meter-title {
  font-weight: 600;
}

.bx--chart-holder {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.bx--chart-holder.filled {
  background-color: #ffffff;
}
.bx--chart-holder.filled .bx--cc--chart-wrapper {
  background-color: #ffffff;
}
.bx--chart-holder .DONT_STYLE_ME_css_styles_verifier {
  overflow: hidden;
  opacity: 0;
}

.bx--chart-holder.fullscreen,
.bx--chart-holder:-webkit-full-screen {
  /* !important is used here to get closer to
  a truly fullscreen experience */
  width: 100% !important;
  height: 100% !important;
  max-width: unset !important;
  max-height: unset !important;
  padding: 2em;
}

.bx--cc--chart-wrapper {
  overflow: visible;
}

